.swiper {
   padding: 30px !important;
    height: 450px;
    background-image: url('../../assets/img/pictures/variedades.png');
    background: #183c55;

  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background-image: url('../../assets/img/pictures/variedades.png');
    background: #183c55;
  
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }


  .divPSlider h3{
    color: white;
    margin: 20px;
  }
  